import { colors } from '../../styles/theme'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'

const MarketingContactSection = styled.div`
  h1 {
    margin-bottom: 10px;
    font-family: ${({ theme }) => theme.fonts.primary};
  }
  p {
    font-size: 14px;
    line-height: 20px;
  }
  a {
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-size: 0 1px;
    transition: background-size 350ms cubic-bezier(0.645, 0.045, 0.355, 1);
    background-image: ${props =>
      `linear-gradient(${props.theme.colors.silver}, ${props.theme.colors.silver});`};
    padding-bottom: 5px;

    &:hover {
      background-size: 100% 1px;
    }
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
    }
  }
`

const MarketingContactSectionItem = styled.div`
  padding: 28px 30px 28px;
  margin: 0 -30px;
  background: ${props => props.background};

  &:first-child {
    padding-top: 56px;
  }

  &:nth-child(2) {
    padding-bottom: 56px;
  }

  &:nth-child(3) {
    padding-top: 56px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(45)} 0 0;
    margin: 0;
    background: ${({ theme }) => theme.colors.secondary};
    &:first-child {
      padding-top: 0;
    }

    &:nth-child(2) {
      padding-bottom: 0;
    }

    &:nth-child(3) {
      padding-top: 0;
    }
    &:last-child {
      padding: 0;
    }
  }
`

export function MarketingSection({ marketingTexts }) {
  return (
    <MarketingContactSection>
      {marketingTexts.map((textObj, i) => {
        return (
          <MarketingContactSectionItem
            background={i < 2 ? '#F3F3F3' : colors.secondary}
            key={textObj.id}
          >
            {documentToReactComponents(textObj?.text?.json)}
          </MarketingContactSectionItem>
        )
      })}
    </MarketingContactSection>
  )
}
