import React, { useState } from 'react'
import styled from 'styled-components'
import AccordionItem from '../AccordionItem'
import { desktopVw } from '../../styles/utils'

const StyledAccordion = styled.div`
  h2 {
    font-size: 12px;
    line-height: 20px;
    font-weight: 500;
    max-width: 90%;
    text-transform: none;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    h2 {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(30)};
    }
  }
`

const StyledAccordionItem = styled(AccordionItem)`
  border-bottom: none;
  .accordion-label-container {
    padding: 10px 0;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .accordion-label-container {
      padding: ${desktopVw(5)} 0;
    }
  }
`

const Accordion = ({
  className,
  items,
  activeIndex,
  toggleMode = false,
  trackParams,
}) => {
  const [toggle, setToggle] = useState(activeIndex)

  return (
    <StyledAccordion className={className}>
      {items.map((item, i) => {
        const isOpen = Array.isArray(activeIndex)
          ? activeIndex.includes(i)
          : toggle === i
        return (
          <StyledAccordionItem
            key={`${item.id}${i}`}
            title={item.title}
            json={item.text && item.text.json}
            open={isOpen}
            toggle={toggleMode ? () => setToggle(i) : null}
            trackParams={trackParams}
            index={i}
          />
        )
      })}
    </StyledAccordion>
  )
}

export default Accordion
