import { useEffect, useRef } from 'react'
import theme from '../styles/theme'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

function getChatBotSelector(selector, idElm = 'chat-button') {
  const iframe = document.getElementById(idElm)
  const iDoc = iframe?.contentWindow || iframe?.contentDocument
  return iDoc?.document?.querySelector(selector)
}

function useGorgiasChat() {
  const chatBotDetected = useRef(false)
  const chatBotOpened = useRef(false)
  const mobileChatBotOpened = useRef(false)

  useEffect(() => {
    const script = document.createElement('script')
    script.id = 'gorgias-chat-widget-install'
    script.innerHTML = `!function(_){_.GORGIAS_CHAT_APP_ID="8260",_.GORGIAS_CHAT_BASE_URL="us-east1-898b.production.gorgias.chat",_.GORGIAS_API_BASE_URL="config.gorgias.chat";var e=new XMLHttpRequest;e.open("GET","https://config.gorgias.chat/applications/8260",!0),e.onload=function(t){if(4===e.readyState)if(200===e.status){var n=JSON.parse(e.responseText);if(!n.application||!n.bundleVersion)throw new Error("Missing fields in the response body - https://config.gorgias.chat/applications/8260");if(_.GORGIAS_CHAT_APP=n.application,_.GORGIAS_CHAT_BUNDLE_VERSION=n.bundleVersion,n&&n.texts&&(_.GORGIAS_CHAT_TEXTS=n.texts),n&&n.sspTexts&&(_.GORGIAS_CHAT_SELF_SERVICE_PORTAL_TEXTS=n.sspTexts),!document.getElementById("gorgias-chat-container")){var o=document.createElement("div");o.id="gorgias-chat-container",document.body.appendChild(o);var r=document.createElement("script");r.setAttribute("defer",!0),r.src="https://client-builds.production.gorgias.chat/{bundleVersion}/static/js/main.js".replace("{bundleVersion}",n.bundleVersion),document.body.appendChild(r)}}else console.error("Failed request GET - https://config.gorgias.chat/applications/8260")},e.onerror=function(_){console.error(_)},e.send()}(window||{});`

    document.head.appendChild(script)

    let chatBotInterval
    let chatBotSelector

    chatBotInterval = setInterval(() => {
      if (chatBotInterval && chatBotDetected.current)
        clearInterval(chatBotInterval)
      else {
        chatBotSelector = getChatBotSelector('[aria-label="Open the chat"]')
        if (chatBotSelector) {
          chatBotSelector.style.display = 'none'
          chatBotSelector.addEventListener('click', onCloseListener)
          chatBotDetected.current = true
        }
      }
    }, 100)

    return () => {
      if (chatBotSelector)
        chatBotSelector.removeEventListener('click', onCloseListener)
    }
  }, [])

  const onCloseListener = () => {
    const mq = window.matchMedia(
      `(max-width: ${theme.breakpoints.desktopSmall}px)`,
    )
    const selector = mq.matches
      ? '[aria-label="Open the chat"]'
      : '[aria-label="Close the chat"]'
    const chatBotSelector = getChatBotSelector(selector)
    if (chatBotSelector && chatBotOpened.current) {
      if (!mq.matches)
        eventTracker.track(EventType.MODAL_CLOSE, {
          action: 'gorgias_chat',
        })
      chatBotSelector.style.display = 'none'
      chatBotOpened.current = false
    }
  }

  const mobileCloseListener = () => {
    eventTracker.track(EventType.MODAL_CLOSE, {
      action: 'gorgias_chat',
    })
  }

  const openGorgiasChat = () => {
    const chatBotSelector = getChatBotSelector('[aria-label="Open the chat"]')
    chatBotOpened.current = true

    let chatBotInterval

    chatBotInterval = setInterval(() => {
      if (chatBotInterval && mobileChatBotOpened.current)
        clearInterval(chatBotInterval)
      else {
        const chatBotSelector = getChatBotSelector(
          '[aria-label="Close the chat window"]',
          'chat-window',
        )
        if (chatBotSelector) {
          chatBotSelector.addEventListener('click', mobileCloseListener)
          mobileChatBotOpened.current = true
        }
      }
    }, 100)

    if (chatBotSelector) {
      chatBotSelector.style.display = 'block'
      chatBotSelector.click()
      eventTracker.track(EventType.MODAL_OPEN, {
        action: 'gorgias_chat',
      })
    }
  }

  return { openGorgiasChat }
}

export default useGorgiasChat
