import React from 'react'
import styled from 'styled-components'
import { graphql } from 'gatsby'
import SEO from '../components/SEO'
import { desktopVw } from '../styles/utils'
import Accordion from '../components/ContactUs/Accordion'
import useGorgiasChat from '../hooks/useGorgiasChat'
import { BannerSection } from '../components/ContactUs/BannerSection'
import { ChatSection } from '../components/ContactUs/ChatSection'
import { MarketingSection } from '../components/ContactUs/MarketingSection'

const Section = styled.section`
  padding: 62px 30px 40px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(80)} ${desktopVw(106)};
  }
`

const ChatFAQContainer = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    top: ${desktopVw(-140)};
  }
`

const FAQWrapper = styled.div`
  margin: 43px 0;

  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: 34px;
    margin-bottom: 10px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    padding: ${desktopVw(60)} ${desktopVw(81)} 0;
    background: ${({ theme }) => theme.colors.secondary};
    flex-basis: 50%;
    h1 {
      font-size: ${desktopVw(36)};
      line-height: ${desktopVw(60)};
    }
  }
`

function ContactUs({
  data: {
    contentfulPage: { sections, seoTitle, seoDescription, metaImage, faq },
  },
}) {
  const textSections = sections.filter(
    section => section.__typename === 'ContentfulTextSection',
  )
  const svgSections = sections.filter(
    section => section.__typename === 'ContentfulSvgWithText',
  )

  const [contactUsText, chatWithUsText, ...marketingTexts] = textSections
  const chatSvg = svgSections[2]
  const orderSvgs = svgSections.slice(0, 2)

  const { openGorgiasChat } = useGorgiasChat()

  return (
    <Section>
      <SEO
        title={seoTitle}
        description={seoDescription}
        image={metaImage ? metaImage.fluid.src : null}
      />
      {sections.map(s => {
        switch (s.__typename) {
          case 'ContentfulBanner':
            return (
              <>
                <BannerSection
                  contactUsText={contactUsText}
                  orderSvgs={orderSvgs}
                  section={s}
                />
                <ChatFAQContainer>
                  <ChatSection
                    chatWithUsText={chatWithUsText}
                    chatSvg={chatSvg}
                    onClick={openGorgiasChat}
                  />
                  {faq && (
                    <FAQWrapper>
                      <h1>FAQs</h1>
                      <Accordion items={faq} />
                    </FAQWrapper>
                  )}
                </ChatFAQContainer>
                <MarketingSection marketingTexts={marketingTexts} />
              </>
            )
          default:
            return null
        }
      })}
    </Section>
  )
}

export default ContactUs

export const ContactUsQuery = graphql`
  query ContactUs($id: String!) {
    contentfulPage(id: { eq: $id }) {
      seoTitle
      seoDescription
      sections {
        ... on ContentfulBanner {
          __typename
          id
          image {
            description
            fluid(maxWidth: 960) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          imageDesktop {
            description
            fluid(maxWidth: 3840) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
        }
        ... on ContentfulSvgWithText {
          ...SVGWithText
        }
        ... on ContentfulTextSection {
          __typename
          id
          text {
            json
          }
        }
      }
      faq {
        id
        title
        text {
          json
        }
      }
    }
  }
`
