import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'

const ChatWithUsContainer = styled.div`
  padding: 46px 30px 60px;
  margin: 0 -30px 0;
  background: ${({ theme }) => theme.colors.lightPink};
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: 34px;
    line-height: ${mobileVw(60)};
    font-weight: 400;
    margin-bottom: 10px;
  }
  p {
    font-size: 14px;
    line-height: 20px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(60)} ${desktopVw(79)} ${desktopVw(64)};
    margin: 0;
    flex-basis: 50%;
    h1 {
      font-size: ${desktopVw(36)};
      line-height: ${desktopVw(60)};
      margin-bottom: ${desktopVw(16)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
    }
  }
`

const UnderlineText = styled.div`
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: 0 1px;
  transition: background-size 350ms cubic-bezier(0.645, 0.045, 0.355, 1);
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.silver}, ${props.theme.colors.silver});`};
  padding-bottom: 5px;
  &:hover {
    background-size: 100% 1px;
  }
  cursor: pointer;
`

const ChatWithSVG = styled.div`
  display: flex;
  align-items: center;
  margin-top: 29px;
  img {
    margin-right: 18px;
  }
  p {
    font-size: 20px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    img {
      margin-right: ${desktopVw(33)};
    }
    p {
      font-size: ${desktopVw(20)};
    }
  }
`

export function ChatSection(props) {
  return (
    <ChatWithUsContainer>
      {documentToReactComponents(props.chatWithUsText?.text?.json)}
      <ChatWithSVG>
        <img
          src={props.chatSvg?.svgDesktop?.file?.url}
          alt={props.chatSvg?.entryTitle}
        />
        <UnderlineText onClick={props.onClick}>
          {documentToReactComponents(props.chatSvg?.titleDesktop?.json)}
        </UnderlineText>
      </ChatWithSVG>
    </ChatWithUsContainer>
  )
}
