import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { getLinkPath, imageSources } from '../../utils'
import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import { desktopVw, mobileVw } from '../../styles/utils'
import { Link } from 'gatsby'

const BannerContainer = styled.div`
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: ${mobileVw(60)};
    line-height: ${mobileVw(60)};
    font-weight: 400;
    margin-bottom: 62px;
  }
  p {
    font-size: 14px;
    line-height: 20px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    height: ${desktopVw(745)};
    h1 {
      font-size: ${desktopVw(70)};
      margin-bottom: ${desktopVw(36)};
      line-height: ${desktopVw(60)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
    }
  }
`

const StyledImage = styled(Image)`
  margin: 45px -30px 0 -30px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute !important;
    margin: 0 ${desktopVw(-80)} 0 ${desktopVw(-80)};
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -1;
  }
`

const TextContainer = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    top: ${desktopVw(117)};
    left: ${desktopVw(708)};
    width: ${desktopVw(413)};
  }
`

const SVGContainer = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 26px;
  img {
    margin-right: 18px;
  }
  p {
    font-size: 20px;
    font-weight: 500;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    img {
      margin-right: ${desktopVw(36)};
    }
    p {
      font-size: ${desktopVw(20)};
    }
  }
`

const ContactUsText = styled.div`
  margin-bottom: 26px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(26)};
  }
`

const UnderlineText = styled(Link)`
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: 0 1px;
  transition: background-size 350ms cubic-bezier(0.645, 0.045, 0.355, 1);
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.silver}, ${props.theme.colors.silver});`};
  padding-bottom: 5px;
  &:hover {
    background-size: 100% 1px;
  }
  cursor: pointer;
`

export function BannerSection({ contactUsText, orderSvgs, section }) {
  return (
    <BannerContainer>
      <TextContainer>
        <ContactUsText>
          {documentToReactComponents(contactUsText?.text?.json)}
        </ContactUsText>
        {orderSvgs.map((svg, idx) => (
          <SVGContainer>
            <img src={svg?.svgDesktop?.file?.url} alt={svg?.entryTitle} />
            <UnderlineText
              to={getLinkPath(svg?.textLink)}
              state={{ activeAccordion: idx }}
            >
              {documentToReactComponents(svg?.titleDesktop?.json)}
            </UnderlineText>
          </SVGContainer>
        ))}
      </TextContainer>
      <StyledImage
        fluid={imageSources(section?.image, section?.imageDesktop)}
        alt={section?.image?.description || section?.image?.title}
      />
    </BannerContainer>
  )
}
